p, ul, a {
    margin: 0;
    list-style-type: none;
    text-decoration: none;
}

.footer {
    min-height: 300px;
    display: flex;
    background-image: url('../../img/texture.jpg');
    background-size: 20px;
    justify-content: space-evenly;
    align-items: center;

    @media(max-width: 700px) {
        display: grid;

    }

    &__privacy {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;

        @media(max-width:700px) {
            right: 25px;
            position: relative;
        }

        &__text {
            font-family: Montserrat-Regular;
            font-size: 11px;
            color: #fff;
            max-width: 140px;
            margin-top: 10px;
        }
    }

    &__privacy img{
        width: 142px;
    }

    &__nav {
        @media(max-width: 700px) {
            margin-top: 20px;
        }
    }

    &__nav li{
        font-family: Montserrat-SemiBold;
        font-size: 20px;
        color: #fff;
        margin-top: 10px;
        transition: 0.2s;
    }


    &__nav li:hover{
        color: #8a8a8a;
    }

    &__contact {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;

        @media(max-width: 700px) {
            margin-top: 20px;
        }

        &__whatsapp {
            font-family: Montserrat-ExtraLight;
            font-size: 20px;
            display: flex;
            align-items: center;
            &__number {
                color: #fff;
                margin-left: 10px;
                margin-bottom: 5px;
            }
        }

        &__icons {
            margin-top: 50px;

            @media(max-width: 700px) {
                margin-top: 30px;
            }
        }

        &__icons a {
            margin-right: 50px;
        }
    }
}